'use client';

import { useEffect, useState } from 'react';

interface PaymentToastProps {
  show: boolean;
  from: string; // e.g. "Grok"
  to: string; // e.g. "Organizer"
  amount: number;
}

export default function PaymentToast({ show, from, to, amount }: PaymentToastProps) {
  if (!show) return null;

  return (
    <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 z-50 pointer-events-none">
       <div className="relative">
          {/* Coin Animation */}
          <div className="absolute bottom-0 left-1/2 -translate-x-1/2 animate-bounce-custom">
            <div className="w-12 h-12 bg-yellow-400 rounded-full border-4 border-yellow-600 shadow-[0_0_30px_gold] flex items-center justify-center text-2xl font-bold text-yellow-900">
                $
            </div>
          </div>
          
          {/* Text Feedback */}
          <div className="absolute top-16 left-1/2 -translate-x-1/2 whitespace-nowrap bg-black/80 text-yellow-400 px-4 py-2 rounded-full border border-yellow-500 backdrop-blur-md animate-fade-in-up">
              <span className="font-bold">{from}</span> paid <span className="font-mono text-white">{amount} USDC</span> via x402
          </div>
       </div>

       <style jsx>{`
        @keyframes bounce-custom {
            0%, 100% { transform: translate(-50%, 0); }
            50% { transform: translate(-50%, -100px) scale(1.2); }
        }
        .animate-bounce-custom {
            animation: bounce-custom 1s ease-in-out infinite;
        }
        .animate-fade-in-up {
            animation: fadeInUp 0.5s ease-out forwards;
        }
        @keyframes fadeInUp {
            from { opacity: 0; transform: translate(-50%, 20px); }
            to { opacity: 1; transform: translate(-50%, 0); }
        }
       `}</style>
    </div>
  );
}
