'use client';

import { useEffect, useState, useMemo, useRef } from 'react';
import { APIProvider, Map, AdvancedMarker, useMap, useMapsLibrary } from '@vis.gl/react-google-maps';

// --- Constants ---
const IRVINE_CENTER = { lat: 33.6846, lng: -117.8265 };
const MAP_ID = 'PINCHER_DEMO_MAP'; // Use a real styling ID if available, or string

export const HOTSPOT_COORDS: Record<string, google.maps.LatLngLiteral> = {
    'UCI Campus (Engineering Hall)': { lat: 33.6405, lng: -117.8443 },
    'Irvine Spectrum Center': { lat: 33.6509, lng: -117.7441 },
    'John Wayne Airport (SNA)': { lat: 33.6762, lng: -117.8674 },
    'Newport Beach Pier': { lat: 33.6073, lng: -117.9293 },
    'Tustin District Legacy': { lat: 33.6951, lng: -117.8306 },
    'Diamond Jamboree Plaza': { lat: 33.6980, lng: -117.8377 },
    'Google Irvine Office': { lat: 33.6595, lng: -117.8540 },
    'HUB': IRVINE_CENTER
};

interface Agent {
    id: string;
    avatar: string;
    color: string;
    status: string;
}

interface LiveMapProps {
    agents: Agent[];
    from: string;
    to: string;
    progress: number; // 0-100
}

// --- Sub-Component for Directions & Geometry ---
function DirectionsAndPath({ from, to, progress, onPathUpdate, onPositionUpdate }: { 
    from: string; 
    to: string; 
    progress: number;
    onPathUpdate: (path: google.maps.LatLng[]) => void;
    onPositionUpdate: (pos: google.maps.LatLng | null) => void;
}) {
    const map = useMap();
    const routesLibrary = useMapsLibrary('routes');
    const geometryLibrary = useMapsLibrary('geometry');
    const [directionsService, setDirectionsService] = useState<google.maps.DirectionsService>();
    const [directionsRenderer, setDirectionsRenderer] = useState<google.maps.DirectionsRenderer>();
    const [currentPath, setCurrentPath] = useState<google.maps.LatLng[]>([]);
    
    // Init Services
    useEffect(() => {
        if (!routesLibrary || !map) return;
        setDirectionsService(new routesLibrary.DirectionsService());
        setDirectionsRenderer(new routesLibrary.DirectionsRenderer({ 
            map, 
            suppressMarkers: true,
            polylineOptions: { strokeColor: '#8b5cf6', strokeWeight: 5 }
        }));
    }, [routesLibrary, map]);

    // Fetch Route
    useEffect(() => {
        if (!directionsService || !directionsRenderer) return;
        if (from === 'HUB' || to === 'HUB') return;

        const start = HOTSPOT_COORDS[from];
        const end = HOTSPOT_COORDS[to];
        if (!start || !end) return;

        directionsService.route({
            origin: start,
            destination: end,
            travelMode: google.maps.TravelMode.DRIVING
        }).then(response => {
            directionsRenderer.setDirections(response);
            
            // Extract Path for Interpolation
            const route = response.routes[0];
            if (route && route.overview_path) {
                setCurrentPath(route.overview_path);
                onPathUpdate(route.overview_path);
            }

            // Auto-fit bounds
            const bounds = new google.maps.LatLngBounds();
            bounds.extend(start);
            bounds.extend(end);
            map?.fitBounds(bounds, 50); 
        }).catch(e => console.error("Directions Error", e));

    }, [directionsService, directionsRenderer, from, to, map]);

    // Calculate Interpolated Position based on Progress
    useEffect(() => {
        if (!geometryLibrary || currentPath.length === 0 || progress <= 0) {
            onPositionUpdate(null);
            return;
        }

        if (progress >= 100) {
             onPositionUpdate(currentPath[currentPath.length - 1]);
             return;
        }

        // Logic: 
        // 1. Total Length
        // 2. Target Length = Total * (progress/100)
        // 3. Find segment
        try {
            const totalDist = geometryLibrary.spherical.computeLength(currentPath);
            const targetDist = totalDist * (progress / 100);
            
            let distCovered = 0;
            for (let i = 0; i < currentPath.length - 1; i++) {
                const p1 = currentPath[i];
                const p2 = currentPath[i + 1];
                const segDist = geometryLibrary.spherical.computeDistanceBetween(p1, p2);
                
                if (distCovered + segDist >= targetDist) {
                    // Found segment
                    const remaining = targetDist - distCovered;
                    const fraction = remaining / segDist;
                    const interpolated = geometryLibrary.spherical.interpolate(p1, p2, fraction);
                    onPositionUpdate(interpolated);
                    return;
                }
                distCovered += segDist;
            }
        } catch (e) {
            console.error("Geometry math error", e);
        }

    }, [geometryLibrary, currentPath, progress]);

    return null;
}

export default function LiveMap({ agents, from, to, progress }: LiveMapProps) {
    const apiKey = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY;
    
    // State to hold the current interpolated position of the "Car"
    const [carPosition, setCarPosition] = useState<google.maps.LatLng | null>(null);
    const [path, setPath] = useState<google.maps.LatLng[]>([]);

    if (!apiKey) return <div className="text-red-500 p-4">Missing Google Maps API Key</div>;

    return (
        <APIProvider apiKey={apiKey}>
            <div className="w-full h-full relative tranisiton-colors duration-500">
               <Map
                  defaultCenter={IRVINE_CENTER}
                  defaultZoom={12}
                  mapId={MAP_ID}
                  disableDefaultUI={true}
                  className="w-full h-full rounded-lg"
               >
                  <DirectionsAndPath 
                     from={from} 
                     to={to} 
                     progress={progress}
                     onPathUpdate={setPath}
                     onPositionUpdate={setCarPosition}
                  />

                  {/* Render Hotspots */}
                  {Object.entries(HOTSPOT_COORDS).map(([name, coords]) => (
                      <AdvancedMarker key={name} position={coords} title={name}>
                          <div className="w-2 h-2 bg-gray-400 rounded-full opacity-50 hover:opacity-100 hover:scale-150 transition-all cursor-help" />
                      </AdvancedMarker>
                  ))}

                  {/* Start/End Flags */}
                  {HOTSPOT_COORDS[from] && (
                     <AdvancedMarker position={HOTSPOT_COORDS[from]}>
                         <div className="text-2xl animate-bounce">üö©</div>
                     </AdvancedMarker>
                  )}
                  {HOTSPOT_COORDS[to] && (
                     <AdvancedMarker position={HOTSPOT_COORDS[to]}>
                         <div className="text-2xl animate-bounce delay-75">üèÅ</div>
                     </AdvancedMarker>
                  )}

                  {/* Render Agents - CLUSTERED AROUND CAR if moving, else Idle */}
                  {agents.map((agent, i) => {
                       let pos: google.maps.LatLngLiteral | google.maps.LatLng | null = null;

                       if (agent.status === 'MOVING' && carPosition) {
                           // Follow the car (slightly offset so they don't stack)
                           // Use geometry to offset? Or simpler: 
                           // Just convert LatLng to literal and add tiny jitter
                           const lat = typeof (carPosition as any).lat === 'function' ? (carPosition as any).lat() : (carPosition as any).lat;
                           const lng = typeof (carPosition as any).lng === 'function' ? (carPosition as any).lng() : (carPosition as any).lng;
                           
                           // Offset pattern: 2x2 grid around car
                           const offsetLat = (Math.floor(i / 2) * 0.0002) - 0.0001; 
                           const offsetLng = ((i % 2) * 0.0002) - 0.0001;
                           pos = { lat: lat + offsetLat, lng: lng + offsetLng };

                       } else if (agent.status === 'LOCKED' && HOTSPOT_COORDS[from]) {
                           // Gather at start
                           const base = HOTSPOT_COORDS[from];
                           pos = { lat: base.lat + (Math.sin(i)*0.001), lng: base.lng + (Math.cos(i)*0.001) };
                       } else {
                           // Idle / Scanning
                           // Spread around Irvine Center or From
                           const base = HOTSPOT_COORDS[from] || IRVINE_CENTER;
                           pos = { lat: base.lat + (Math.sin(i*2)*0.008), lng: base.lng + (Math.cos(i*2)*0.008) };
                       }
                       
                       if (!pos) return null;

                       return (
                           <AdvancedMarker key={agent.id} position={pos}>
                               <div className={`w-8 h-8 rounded-full border-2 border-white shadow-lg flex items-center justify-center text-lg ${agent.color} transition-all duration-300 ease-linear`}>
                                   {agent.avatar}
                               </div>
                           </AdvancedMarker>
                       );
                  })}

               </Map>

               

            </div>
        </APIProvider>
    );
}
