'use client';

import { useEffect, useState } from 'react';

interface Agent {
    id: string;
    name: string;
    color: string;
    avatar: string;
    address: string; // Added to fix type error
}

interface SplitBillOverlayProps {
    show: boolean;
    agents: Agent[];
    totalAmount: number;
    onComplete: () => void;
    isLiveMode: boolean; // New: Are we in real mode?
    userAddress?: string; // New: Identify the user
    txStatus?: 'IDLE' | 'WAITING' | 'PENDING' | 'SUCCESS' | 'ERROR'; // New: External control
    txHash?: string;
}

export default function SplitBillOverlay({ show, agents, totalAmount, onComplete, isLiveMode, userAddress, txStatus = 'IDLE', txHash }: SplitBillOverlayProps) {
    const [internalStep, setInternalStep] = useState<'IDLE' | 'SENDING' | 'CONFIRMING' | 'DONE'>('IDLE');

    // Sync internal animation with external status if in Live Mode
    useEffect(() => {
        if (!show) {
            setInternalStep('IDLE');
            return;
        }

        if (isLiveMode) {
             // In Live Mode, we listen to txStatus
             if (txStatus === 'WAITING') setInternalStep('SENDING'); // Stall at sending
             if (txStatus === 'PENDING') setInternalStep('CONFIRMING');
             if (txStatus === 'SUCCESS') {
                 setInternalStep('DONE');
                 setTimeout(onComplete, 2000);
             }
        } else {
            // Simulation Mode (Original Animation)
            setInternalStep('SENDING');
            setTimeout(() => {
                setInternalStep('CONFIRMING');
                setTimeout(() => {
                    setInternalStep('DONE');
                    setTimeout(() => {
                         onComplete();
                         setInternalStep('IDLE');
                    }, 1500);
                }, 1500);
            }, 2000);
        }
    }, [show, isLiveMode, txStatus, onComplete]);

    if (!show && internalStep === 'IDLE') return null;

    const splitAmount = (totalAmount / 4).toFixed(4);

    return (
        <div className="absolute inset-0 z-50 flex items-center justify-center bg-black/80 backdrop-blur-md animate-in fade-in duration-300">
            <div className="relative w-[600px] h-[400px]">
                
                {/* Central Escrow Vault */}
                <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 flex flex-col items-center z-20">
                    <div className={`w-24 h-24 rounded-2xl border-2 flex items-center justify-center text-4xl shadow-[0_0_40px_rgba(59,130,246,0.4)] transition-all duration-500 ${internalStep === 'CONFIRMING' || (isLiveMode && txStatus === 'WAITING') ? 'bg-blue-600 border-blue-400 scale-110' : internalStep === 'DONE' ? 'bg-green-600 border-green-400 scale-100' : 'bg-slate-900 border-slate-700'}`}>
                        {internalStep === 'DONE' ? '‚úÖ' : (isLiveMode && txStatus === 'WAITING') ? '‚úçÔ∏è' : internalStep === 'CONFIRMING' ? '‚è≥' : 'üè¶'}
                    </div>
                    <div className="mt-4 text-white font-mono font-bold text-xl bg-black/50 px-4 py-2 rounded-lg border border-white/10">
                        ESCROW VAULT
                    </div>
                    {internalStep === 'DONE' && (
                        <div className="absolute -bottom-20 flex flex-col items-center animate-in zoom-in slide-in-from-bottom-2">
                             <div className="bg-green-500/20 text-green-400 border border-green-500/50 px-4 py-1 rounded-full text-sm font-bold mb-1">
                                 ON-CHAIN CONFIRMED
                             </div>
                             {txHash && (
                                 <a href={`https://sepolia.basescan.org/tx/${txHash}`} target="_blank" className="text-[10px] text-blue-400 hover:underline">
                                     View on BaseScan ‚Üó
                                 </a>
                             )}
                        </div>
                    )}
                </div>

                {/* Agents and Flow Lines */}
                {agents.slice(0, 4).map((agent, i) => {
                    const isTop = i < 2;
                    const isLeft = i % 2 === 0;
                    
                    const top = isTop ? '15%' : '85%';
                    const left = isLeft ? '15%' : '85%';
                    
                    const isUser = isLiveMode && userAddress && agent.address.toLowerCase() === userAddress.toLowerCase();

                    return (
                        <div key={agent.id} className="absolute w-full h-full pointer-events-none">
                             {/* Agent Node */}
                             <div 
                                className="absolute flex flex-col items-center gap-2 transition-all duration-500"
                                style={{ top, left, transform: 'translate(-50%, -50%)' }}
                             >
                                 <div className={`w-16 h-16 rounded-full border-4 ${isUser ? 'border-yellow-400 shadow-[0_0_20px_gold]' : 'border-white/20'} flex items-center justify-center text-3xl shadow-lg relative ${agent.color} z-10`}>
                                     {agent.avatar}
                                     
                                     {/* Minus amount popup */}
                                     {internalStep === 'SENDING' && (
                                         <div className="absolute -top-8 text-red-400 font-bold text-sm animate-fade-out-up">
                                             -{splitAmount}
                                         </div>
                                     )}

                                     {/* User Indicator */}
                                     {isUser && (
                                         <div className="absolute -bottom-2 bg-yellow-400 text-black text-[10px] font-bold px-2 rounded-full">
                                             YOU
                                         </div>
                                     )}
                                 </div>
                                 <div className="text-sm font-bold text-slate-200 bg-black/60 px-3 py-1 rounded-full backdrop-blur-md border border-white/10">
                                     {agent.name.split(' (')[0]}
                                 </div>
                                 
                                 {/* Status Text for User */}
                                 {isUser && txStatus === 'WAITING' && (
                                     <div className="absolute top-20 w-32 text-center text-xs font-bold text-blue-400 animate-pulse bg-black/80 p-2 rounded border border-blue-500/50">
                                         Receiving Payment...
                                     </div>
                                 )}
                             </div>

                             {/* Connection Logic / Animation */}
                             {internalStep !== 'IDLE' && (
                                <svg className="absolute inset-0 w-full h-full pointer-events-none overflow-visible">
                                    <defs>
                                        <marker id={`arrow-${i}`} markerWidth="10" markerHeight="10" refX="9" refY="3" orient="auto" markerUnits="strokeWidth">
                                            <path d="M0,0 L0,6 L9,3 z" fill={internalStep === 'DONE' ? '#4ade80' : '#60a5fa'} />
                                        </marker>
                                    </defs>
                                    
                                    {/* Path from Agent to Center (or User) */}
                                    {(() => {
                                        const startX = isLeft ? 90 : 510;
                                        const startY = isTop ? 60 : 340;
                                        const endX = 300;
                                        const endY = 200;
                                        
                                        // If this is the User node, we don't draw a line FROM it, we draw TO it (conceptually via Escrow)
                                        // For simplicity: All agents send to Center. Center resolves to User.
                                        
                                        return (
                                            <>
                                                {/* Base Line */}
                                                <line 
                                                    x1={startX} y1={startY} x2={endX} y2={endY} 
                                                    stroke={internalStep === 'DONE' ? '#4ade80' : 'rgba(255,255,255,0.1)'} 
                                                    strokeWidth="2" 
                                                    strokeDasharray="4 4"
                                                />
                                                
                                                {/* Moving Particle / Coin */}
                                                {(internalStep === 'SENDING' || internalStep === 'CONFIRMING') && !isUser && (
                                                    <circle r="6" fill="#60a5fa">
                                                        <animateMotion 
                                                            dur="1.5s" 
                                                            repeatCount="indefinite"
                                                            fill="freeze"
                                                            path={`M${startX},${startY} L${endX},${endY}`}
                                                            keyPoints="0;1"
                                                            keyTimes="0;1"
                                                            calcMode="linear"
                                                        />
                                                    </circle>
                                                )}
                                            </>
                                        );
                                    })()}
                                </svg>
                             )}
                        </div>
                    );
                })}
            </div>
        </div>
    );
}
