'use client';

interface MiniReceiptProps {
  show: boolean;
  txHash: string;
  amount: number;
  from: string;
  to: string;
}

export default function MiniReceipt({ show, txHash, amount, from, to }: MiniReceiptProps) {
  if (!show) return null;

  return (
    <div className="absolute top-20 right-4 z-50 animate-slide-in-right">
      <div className="bg-white rounded-lg shadow-xl border-l-4 border-green-500 overflow-hidden w-64 ring-1 ring-black/5">
        <div className="p-3">
            <div className="flex items-center justify-between mb-2">
                <span className="text-[10px] font-bold text-gray-500 uppercase tracking-wider">On-Chain Receipt</span>
                <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
            </div>
            
            <div className="flex items-center gap-2 mb-2">
                <div className="p-1.5 bg-green-100 rounded text-green-600">
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" /></svg>
                </div>
                <div>
                    <div className="text-sm font-bold text-gray-900">{amount} USDC</div>
                    <div className="text-[10px] text-gray-500">Split Settlement Confirmed</div>
                </div>
            </div>

            <div className="space-y-1 bg-gray-50 p-2 rounded border border-gray-100">
                <div className="flex justify-between text-[10px]">
                    <span className="text-gray-400">From</span>
                    <span className="font-mono text-gray-700">{from}</span>
                </div>
                <div className="flex justify-between text-[10px]">
                    <span className="text-gray-400">To</span>
                    <span className="font-mono text-gray-700">{to}</span>
                </div>
                <div className="flex justify-between text-[10px] items-center pt-1 mt-1 border-t border-gray-200">
                    <span className="text-gray-400">Tx</span>
                    <a 
                        href={`https://sepolia.basescan.org/tx/${txHash}`} 
                        target="_blank" 
                        rel="noreferrer"
                        className="font-mono text-blue-600 hover:underline truncate w-24 text-right"
                    >
                        {txHash.slice(0,6)}...{txHash.slice(-4)}
                    </a>
                </div>
            </div>
        </div>
      </div>
      <style jsx>{`
        @keyframes slideInRight {
            from { transform: translateX(100%); opacity: 0; }
            to { transform: translateX(0); opacity: 1; }
        }
        .animate-slide-in-right {
            animation: slideInRight 0.5s cubic-bezier(0.16, 1, 0.3, 1) forwards;
        }
      `}</style>
    </div>
  );
}
