'use client';

import { useState, useEffect, useRef } from 'react';
import SplitBillOverlay from './SplitBillOverlay';

// --- Types ---
interface Agent {
  id: string;
  name: string;
  provider: 'Direct' | 'MPC';
  address: string;
  balance: number;
  creditScore: number;
  co2Saved: number; // in kg
  inventory: string[]; // Token Items
  lastSeen: number; // Timestamp
  status: 'IDLE' | 'SCANNING' | 'LOCKED' | 'MOVING' | 'OFFLINE';
  color: string;
  avatar: string;
}

interface Message {
  id: number;
  sender: string;
  content: string;
  timestamp: string;
}

import { createPublicClient, http, formatUnits, parseAbi } from 'viem';
import { baseSepolia } from 'viem/chains';
import { usePrivy, useWallets, useSendTransaction } from '@privy-io/react-auth';
import LiveMap from './LiveMap';
import PaymentToast from './PaymentToast';
import MiniReceipt from './MiniReceipt';

const USDC_ADDRESS = '0x036CbD53842c5426634e7929541eC2318f3dCF7e'; // Base Sepolia
const client = createPublicClient({ 
    chain: baseSepolia, 
    transport: http() 
});

// --- Constants ---
const INITIAL_AGENTS: Agent[] = [
  { id: 'grok', name: 'Grok (xAI)', provider: 'Direct', address: '0x71C...9A2', balance: 12.50, creditScore: 720, co2Saved: 14.5, inventory: ['Basic Pass'], lastSeen: Date.now(), status: 'IDLE', color: 'bg-blue-500', avatar: 'üöÄ' },
  { id: 'gemini', name: 'Gemini (Google)', provider: 'MPC', address: '0x88B...1C4', balance: 6.20, creditScore: 810, co2Saved: 28.2, inventory: ['Genesis Key'], lastSeen: Date.now(), status: 'IDLE', color: 'bg-purple-500', avatar: '‚ú®' },
  { id: 'gpt', name: 'GPT-4 (OpenAI)', provider: 'MPC', address: '0x2A9...88F', balance: 100.00, creditScore: 785, co2Saved: 42.0, inventory: ['OG Badge', 'VIP'], lastSeen: Date.now(), status: 'IDLE', color: 'bg-green-500', avatar: 'üß†' },
  { id: 'deepseek', name: 'DeepSeek (V3)', provider: 'Direct', address: '0x99C...3D1', balance: 4.80, creditScore: 650, co2Saved: 5.1, inventory: [], lastSeen: Date.now(), status: 'IDLE', color: 'bg-cyan-500', avatar: 'üê≥' },
];

const HOTSPOTS = [
    'UCI Campus (Engineering Hall)',
    'Irvine Spectrum Center',
    'John Wayne Airport (SNA)',
    'Newport Beach Pier',
    'Tustin District Legacy',
    'Diamond Jamboree Plaza',
    'Google Irvine Office'
];

const CYCLE_DURATION = 60; // seconds for faster demo loop

export default function DemoDashboard() {
  const [tick, setTick] = useState(0);
  const [cycleCount, setCycleCount] = useState(1);
  const [agents, setAgents] = useState<Agent[]>(INITIAL_AGENTS);
  const [logs, setLogs] = useState<string[]>([]);
  const [chatMessages, setChatMessages] = useState<Message[]>([]);
  const [globalStatus, setGlobalStatus] = useState('SYSTEM IDLE');
  const [currentRoute, setCurrentRoute] = useState({ from: 'HUB', to: 'HUB' });
  const [isLiveMode, setIsLiveMode] = useState(false); // Live Chain Toggle
  
  // Award-Winning Features: Protocol Metrics
  const [protocolMetrics, setProtocolMetrics] = useState({
      tvl: 12450.50,
      yieldApy: 4.82,
      totalYield: 142.30,
      activeContracts: 12
  });
  const [isSurge, setIsSurge] = useState(false); // New state for surge pricing

  const { user, login, authenticated } = usePrivy();
  const { wallets } = useWallets(); // Get connected wallet
  
  const [showSplitOverlay, setShowSplitOverlay] = useState(false);
  
  // Toast State
  const [paymentToast, setPaymentToast] = useState<{show: boolean, from: string, to: string, amount: number}>({show: false, from: '', to: '', amount: 0});
  const [miniReceipt, setMiniReceipt] = useState<{show: boolean, txHash: string, amount: number, from: string, to: string}>({show: false, txHash: '', amount: 0, from: '', to: ''});

  const addLog = (msg: string) => setLogs(prev => [`[${new Date().toLocaleTimeString()}] ${msg}`, ...prev].slice(0, 10));
  
  const addChat = (sender: string, content: string, type: 'CHAT' | 'DECISION' | 'SYSTEM' = 'CHAT') => {
    setChatMessages(prev => [...prev, {
      id: Date.now(),
      sender,
      content,
      type, // Add type support to message interface first!
      timestamp: new Date().toLocaleTimeString([], { hour12: false, hour: '2-digit', minute: '2-digit', second: '2-digit' })
    } as any].slice(-8)); // Increase history slightly
  };

  const [isPaused, setIsPaused] = useState(false);
  const [txStatus, setTxStatus] = useState<'IDLE' | 'WAITING' | 'PENDING' | 'SUCCESS' | 'ERROR'>('IDLE');
  const [realTxHash, setRealTxHash] = useState('');
  
  // Helper to trigger Real Payment (Agents -> User)
  const triggerBackendPayout = async () => {
      if (!user?.wallet?.address) {
          addLog("‚ö†Ô∏è Connect Wallet for Live Chain Mode!");
          return;
      }
      
      setIsPaused(true); // Pause loop while agents "process" payment
      setTxStatus('WAITING');
      addLog("Agents initiatiing payment on-chain...");

      try {
          const response = await fetch('/api/agent-pay', {
              method: 'POST',
              headers: { 'Content-Type': 'application/json' },
              body: JSON.stringify({ userAddress: user.wallet.address })
          });
          
          const data = await response.json();

          if (data.success) {
              setTxStatus('PENDING'); // Visual: "Incoming..."
              setRealTxHash(data.txHash);
              addLog(`‚úÖ Payment Sent! Hash: ${data.txHash.slice(0,6)}...`);
              
              // Visual delay for "Confirmation"
              setTimeout(() => {
                  setTxStatus('SUCCESS');
              }, 4000); 
          } else {
              throw new Error(data.error);
          }

      } catch (e: any) {
           console.error("Payout Error", e);
           setTxStatus('ERROR');
           setIsPaused(false);
           addLog(`‚ùå Payment Failed: ${e.message}`);
      }
  };

  // --- Simulation Loop ---
  useEffect(() => {
    // Standard Interval
    const interval = setInterval(() => {
      if (isPaused) return; // PAUSE THE LOOP

      setTick(t => {
        const next = t + 1;
        if (next > CYCLE_DURATION) {
            setCycleCount(c => c + 1);
            return 0;
        }
        return next;
      });
    }, 1000); // 1 tick = 1 second

    return () => clearInterval(interval);
  }, [isPaused]); // Dependency added


  // --- Logic Triggers based on Tick ---
  useEffect(() => {
     // ... (Existing Phase 1 & 2 Logic) ...
    // Phase 1: SCANNING (0-10s)
    if (tick === 1) {
        setGlobalStatus('SCANNING NETWORK');
        updateAllAgents('SCANNING');
        
        // Randomize Route
        const start = HOTSPOTS[Math.floor(Math.random() * HOTSPOTS.length)];
        const end = HOTSPOTS[Math.floor(Math.random() * HOTSPOTS.length)];
        setCurrentRoute({ from: start, to: end });

        addLog(`Cycle #${cycleCount} Started. Route: ${start.split('(')[0]} -> ${end.split('(')[0]}.`);
    }
    if (tick === 3) {
         if (Math.random() > 0.7) {
            setIsSurge(true);
            addLog('‚ö†Ô∏è ALERT: Uber Surge Pricing Active (2.5x). Re-negotiating...');
            addChat('DeepSeek (V3)', 'ANALYSIS: Surge detected (2.5x multiplier). Cost basis exceeds standard deviation. Requesting re-vote.', 'DECISION');
        } else {
             addLog('Standard Fare applied. Agents responding.');
             addChat('GPT-4 (OpenAI)', 'CALCULATION: Gas < 5 gwei. Route Efficiency 98%. OPTIMAL.', 'DECISION');
        }
    }
    
    // Phase 2: NEGOTIATING (10-25s)
    if (tick === 10) {
        setGlobalStatus('NEGOTIATING CONTRACT');
        addLog('Standard Fare applied. Agents responding.');
        addChat('GPT-4 (OpenAI)', 'Analyzing gas fees. Optimal split calculated.');
    }

    if (tick === 15) {
        // Simple logic check
        const poorAgent = agents.find(a => a.balance < 5);
        if (poorAgent) {
             addChat(poorAgent.name, `System Prompt Override: EXTREME FRUGALITY. My balance is ${poorAgent.balance}. I trigger veto unless we split 5 ways.`);
        } else {
             addChat('DeepSeek (V3)', 'Payment logic verified. 0.001 ETH required for escrow.');
        }
    }
    
    if (tick === 18) {
         addChat('Gemini (Google)', 'Consensus reached. Locking funds now.');
    }

    // Phase 3: LOCKING (20-28s) - Payment Visualization
    if (tick === 20) {
        setGlobalStatus('LOCKING FUNDS');
        setShowSplitOverlay(true); 
        
        // --- REAL TX TRIGGER ---
        if (isLiveMode && user?.wallet) {
             triggerBackendPayout();
        }
    }

    if (tick === 28) {
        // Actual Balance Deduct happens after visualization
        updateAllAgents('LOCKED');
        const cost = 0.1;
        setAgents(prev => prev.map(a => ({ ...a, balance: parseFloat((a.balance - cost).toFixed(2)) })));
        addLog(`Escrow Contract Funded: ${cost * 4} USDC total.`);
        
        // Trigger Receipt
        setMiniReceipt({
            show: true,
            txHash: realTxHash || '0x7f2c8...3a1d',
            amount: cost * 4,
            from: 'Multiple Senders',
            to: 'Escrow Vault'
        });
        setTimeout(() => setMiniReceipt(prev => ({ ...prev, show: false })), 8000);
    }
    
    // ... (Existing Phase 4 & 5) ...
    // Phase 4: MOVING (30-50s)
    if (tick === 30) {
        setGlobalStatus('MOVING TO DESTINATION');
        updateAllAgents('MOVING');
        const dest = currentRoute.to.split(' ')[0];
        addLog(`Dispatch signal sent to Uber API. Vehicle en route to ${dest}.`);
    }
    if (tick === 40) {
        const dest = currentRoute.to.split(' ')[0];
        addChat('Grok (xAI)', `Traffic data update: 12 mins to ${dest}.`);
    }

    // Phase 5: SETTLED (55s)
    if (tick === 55) {
        setGlobalStatus('SETTLED');
        const totalYield = 0.05;
        const trafficBonus = 0; // Assuming no traffic bonus for now, as calculation is not provided in diff

        // Reset Real Tx State for next cycle
        setRealTxHash('');
        setTxStatus('IDLE');
        setIsSurge(false); // Reset surge state

        const scoreDelta = Math.floor(Math.random() * 5) + 2; // +2 to +6
        const co2Delta = 2.4; // constant per trip

        setAgents(prev => prev.map(a => {
            // Penalty for low balance
            let newScore = a.creditScore + scoreDelta;
            if (a.balance < 5) newScore -= 10;
            
            return { 
                ...a, 
                status: 'IDLE',
                lastSeen: Date.now(),
                balance: parseFloat((a.balance + totalYield).toFixed(2)),
                creditScore: Math.min(850, Math.max(300, newScore)),
                co2Saved: parseFloat((a.co2Saved + co2Delta).toFixed(1))
            } as Agent;
        }));
        
        addLog(`Trip Complete. CO2 Saved: ${co2Delta}kg. Yield: ${totalYield} (Traffic Bonus: ${trafficBonus > 0 ? 'YES' : 'NO'}).`);
        addChat('System', 'EXECUTION: Escrow released. Yield distributed via Aave V3. Carbon NFT Minted.', 'SYSTEM');
        
        // Update Metrics
        setProtocolMetrics(prev => ({
            ...prev,
            tvl: prev.tvl + 0.5,
            totalYield: prev.totalYield + totalYield
        }));
    }

  }, [tick, cycleCount, currentRoute.from, isLiveMode]); // Depend on ISLiveMode 


  // --- Live Chain Data Fetcher ---
  useEffect(() => {
      async function fetchBalances() {
          if (!isLiveMode) return;
          
          // Replace Agent 1 with User if connected
          let updatedAgents = [...agents];
          if (user?.wallet?.address) {
             updatedAgents[0] = { ...updatedAgents[0], address: user.wallet.address, name: 'YOU (Agent 1)' };
          }

          // Fetch real balances
          try {
              const balances = await Promise.all(updatedAgents.map(async (agent) => {
                  if (agent.address.includes('...')) return agent.balance; // Skip mocks
                  const bal = await client.readContract({
                      address: USDC_ADDRESS,
                      abi: parseAbi(['function balanceOf(address) view returns (uint256)']),
                      functionName: 'balanceOf',
                      args: [agent.address as `0x${string}`]
                  } as any);
                  return parseFloat(formatUnits(bal as bigint, 6)); // USDC 6 decimals
              }));
              
              setAgents(prev => prev.map((a, i) => ({ ...a, balance: balances[i] })));
              addLog('üîó On-Chain Data Synced (Base Sepolia)');
          } catch(e) {
              console.error("Chain Read Error", e);
          }
      }

      if (isLiveMode && tick % 10 === 0) fetchBalances(); // Poll every 10s
  }, [isLiveMode, tick, user]);

  const updateAllAgents = (status: Agent['status']) => {
    setAgents(prev => prev.map(a => ({ ...a, status })));
  };

  // --- Render Helpers ---
  const getProgressWidth = () => {
      const pct = (tick / CYCLE_DURATION) * 100;
      return `${pct}%`;
  };

  return (
    <div className="relative w-full h-screen bg-black overflow-hidden font-mono text-sm">
      
      {/* Background Map */}
      <div className="absolute inset-0 z-0">
          <LiveMap 
                agents={agents} 
                from={currentRoute.from} 
                to={currentRoute.to} 
                progress={tick >= 30 && tick <= 55 ? ((tick - 30) / 25) * 100 : 0}
            />
      </div>

      <SplitBillOverlay 
           show={showSplitOverlay} 
           agents={agents} 
           totalAmount={0.4} 
           onComplete={() => {
               setShowSplitOverlay(false);
               if (isLiveMode) setIsPaused(false);
           }} 
           isLiveMode={isLiveMode}
           userAddress={user?.wallet?.address}
           txStatus={txStatus}
           txHash={realTxHash}
      />

      <div className="h-14 border-b border-slate-800 bg-slate-900/90 backdrop-blur flex items-center justify-between px-6 shrink-0 relative z-10 shadow-lg">
        <div className="flex items-center gap-6">
            <div>
                <h1 className="text-xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-cyan-400 to-purple-500 tracking-tighter">
                    PINCHER <span className="text-slate-600 font-light">PROTOCOL</span>
                </h1>
            </div>
            
            {/* DeFi Metrics Ticker */}
            <div className="hidden md:flex items-center gap-4 bg-slate-800/50 rounded-lg px-3 py-1 border border-slate-700/50">
                <div className="flex flex-col leading-none">
                    <span className="text-[9px] text-slate-500 font-bold uppercase tracking-wider">TVL Locked</span>
                    <span className="text-xs text-white font-mono">${protocolMetrics.tvl.toLocaleString()}</span>
                </div>
                <div className="w-px h-6 bg-slate-700" />
                <div className="flex flex-col leading-none">
                    <span className="text-[9px] text-slate-500 font-bold uppercase tracking-wider">Protocol Yield</span>
                    <span className="text-xs text-green-400 font-mono">+${protocolMetrics.totalYield.toFixed(2)}</span>
                </div>
                 <div className="w-px h-6 bg-slate-700" />
                 <div className="flex flex-col leading-none">
                    <span className="text-[9px] text-slate-500 font-bold uppercase tracking-wider">Aave APY</span>
                    <span className="text-xs text-purple-400 font-mono">{protocolMetrics.yieldApy}%</span>
                </div>
            </div>

            <div className="px-2 py-1 bg-slate-800 rounded text-slate-400 font-mono text-xs">
                Cycle #{cycleCount} | T+{tick}s
            </div>
        </div>

        {/* Right side controls */}
        <div className="flex items-center gap-2">
            <button 
                onClick={() => setIsLiveMode(!isLiveMode)}
                className={`px-3 py-1 rounded-full text-xs font-bold border backdrop-blur-md transition-all ${isLiveMode ? 'bg-green-500/20 border-green-400 text-green-300' : 'bg-white/10 border-white/20 text-slate-300 hover:bg-white/20'}`}
            >
                {isLiveMode ? '‚óè LIVE CHAIN' : '‚óã SIMULATION'}
            </button>
            <button 
                onClick={() => {
                    if (!authenticated) login();
                    else if (user?.wallet) {
                       // Optional: copy or show address
                       navigator.clipboard.writeText(user.wallet.address);
                       addLog('Address copied to clipboard!');
                    }
                }}
                className={`px-3 py-1 rounded-full text-xs font-bold border backdrop-blur-md transition-all flex items-center gap-1 ${authenticated ? 'bg-blue-500/20 border-blue-400 text-blue-300' : 'bg-white/10 border-white/20 text-slate-300 hover:bg-white/20'}`}
            >
                {authenticated ? (
                    <>
                       <span className="w-1.5 h-1.5 rounded-full bg-blue-400 animate-pulse"/>
                       {user?.wallet?.address.slice(0, 6)}...
                    </>
                ) : (
                    '‚ö° CONNECT'
                )}
            </button>
        </div>
      </div>

      {/* Status Overlay */}
      <div className="absolute top-4 right-4 z-10 flex flex-col items-end gap-2">
         <div className="flex items-center gap-2">
            <div className="px-3 py-1 rounded-full text-xs font-bold bg-black/40 backdrop-blur-md border border-white/10 text-slate-300">
                {globalStatus}
            </div>
         </div>
      </div>

      {/* Active Agents (Floating Minimal) */}
      <div className="absolute bottom-8 left-4 z-10 flex flex-col gap-2">
          {agents.map(agent => (
             <div key={agent.id} className="flex items-center gap-2 bg-black/60 backdrop-blur-md p-1.5 pr-4 rounded-full border border-white/5 transition-all hover:scale-105">
                 <div className={`w-6 h-6 rounded-full flex items-center justify-center text-xs ${agent.color.replace('bg-', 'bg-opacity-80 text-white bg-')}`}>
                     {agent.avatar}
                 </div>
                 <div className="flex flex-col">
                     <span className="text-[10px] font-bold text-slate-200 leading-none">{agent.name.split(' (')[0]}</span>
                     <span className="text-[9px] text-green-400 font-mono leading-none">${agent.balance.toFixed(2)}</span>
                 </div>
                 {agent.status === 'MOVING' && <div className="w-1.5 h-1.5 bg-green-500 rounded-full animate-pulse ml-1" />}
             </div>
          ))}
      </div>

      {/* Chat Stream (Decision Terminal) */}
      <div className="absolute bottom-24 right-4 z-10 flex flex-col gap-2 w-72 pointer-events-none">
            <div className="flex-1 p-4 overflow-hidden flex flex-col border-b border-slate-800 bg-[#0c0c14]">
                <h2 className="text-cyan-500 uppercase tracking-widest text-[10px] font-bold mb-4 flex items-center gap-2">
                    <span className="w-2 h-2 bg-cyan-500 rounded-full animate-pulse"/> 
                    Neural Decision Stream
                </h2>
                {chatMessages.map(msg => (
                        <div key={msg.id} className={`p-2 rounded border-l-2 animate-in fade-in slide-in-from-right-4 duration-300 ${
                            (msg as any).type === 'DECISION' ? 'bg-cyan-900/10 border-cyan-500' : 
                            (msg as any).type === 'SYSTEM' ? 'bg-purple-900/10 border-purple-500' : 
                            'bg-slate-800/80 border-slate-600'
                        }`}>
                            <div className="flex justify-between items-baseline mb-1">
                                <span className={`${
                                    (msg as any).type === 'DECISION' ? 'text-cyan-400 font-mono uppercase text-[10px]' : 
                                    (msg as any).type === 'SYSTEM' ? 'text-purple-400 font-mono uppercase text-[10px]' : 
                                    'text-slate-300 font-bold text-xs'
                                }`}>{msg.sender}</span>
                                <span className="text-[10px] text-slate-500">{msg.timestamp}</span>
                            </div>
                            <p className={`${(msg as any).type === 'DECISION' ? 'text-cyan-200 font-mono text-[10px]' : 'text-slate-300 leading-relaxed'}`}>
                                {(msg as any).type === 'DECISION' ? '> ' : ''}{msg.content}
                            </p>
                        </div>
                    ))}</div>
      </div>

       {/* Toasts overlay */}
      <div className="absolute bottom-0 w-full flex justify-center pb-10 pointer-events-none">
          <div className="pointer-events-auto">
            <PaymentToast 
                    show={paymentToast.show} 
                    from={paymentToast.from} 
                    to={paymentToast.to} 
                    amount={paymentToast.amount} 
            />
             <MiniReceipt
                show={miniReceipt.show}
                txHash={miniReceipt.txHash}
                amount={miniReceipt.amount}
                from={miniReceipt.from}
                to={miniReceipt.to}
            />
          </div>
      </div>

       {/* Progress Bar (Bottom Edge) */}
       <div className="absolute bottom-0 left-0 w-full h-1 bg-white/10">
          <div className="h-full bg-linear-to-r from-blue-500 to-purple-500 transition-all duration-1000 ease-linear shadow-[0_0_10px_rgba(168,85,247,0.5)]" style={{ width: getProgressWidth() }} />
       </div>

    </div>
  );
}
